import url from 'fast-url-parser';
import Store from '../lib/store.js'
import $ from '../lib/$.js'

/**
* Query Params
*/
let query = url.parse(window.location.href, true)._query || {}

/**
* DOM Elements
*/
let body = document.querySelector('body')
let versions = $('.versioner')
let versionNav = document.querySelector('.js-versions')
let roleSelect = $('.js-user-role')
let signedInCheckbox = $('.js-signed-in')
let requirementsLink = $('.js-requirements')
let requirementsContent = $('.js-requirements-content')

/**
* Local Store container
*/
let store = Store('arcgis-prototype-state', {
  role: 'admin',
  signedIn: true
})

function setState (state) {
  state = state || store()
  let role = `is-${query.role || state.role}`
  let auth = (query.signedIn === 'true' || state.signedIn) ? 'is-signed-in' : 'is-signed-out'
  body.classList.remove('is-signed-in', 'is-signed-out', 'is-admin', 'is-viewer', 'is-developer')
  body.classList.add(role, auth)
}

store.on('changed', state => {
  setState(state)
})

/**
* Set everything as it should be on load
*/
versions.forEach((version, i) => {
  let link = `<a href="#" class="side-nav-link state-control js-version-link" data-version-index="${i}">${version.dataset.version}</a>`
  versionNav.insertAdjacentHTML('beforeend', link)
})

let activeVersion = $('[data-version="'+query.v+'"]')[0] || $('.versioner.is-active')[0] || versions[0]
if (activeVersion) {
  activeVersion.classList.add('is-active')
  $('.js-version-link')[versions.indexOf(activeVersion)].classList.add('is-active')
  $(`option[value="${store().role}"]`)[0].selected = true
  $('.js-signed-in')[0].checked = store().signedIn
}

function signInOut (e, bool) {
  e.preventDefault()
  let state = store()
  state.signedIn = bool
  store(state)
  location = document.querySelector('body').dataset.path
}

setState()

/**
* Click Handlers
*/
$('.js-version-link').on('click', e => {
  e.preventDefault()
  $('.js-version-link').removeClass('is-active')
  $(e.target).addClass('is-active')
  requirementsContent.addClass('hide')
  versions.removeClass('is-active')
  $(versions[e.target.dataset.versionIndex]).addClass('is-active')
})

$('.js-sign-out').on('click', e => {
  signInOut(e, false)
})
$('.js-sign-in').on('click', e => {
  signInOut(e, true)
})


roleSelect.on('change', e => {
  store(state => {
    state.role = e.target.value
    return state
  })
})

signedInCheckbox.on('click', e => {
  store(state => {
    state.signedIn = e.target.checked
    return state
  })
})

requirementsLink.on('click', e => {
  e.preventDefault()
  versions.removeClass('is-active')
  requirementsContent.removeClass('hide')
})
